.searchContainer> input:focus{
 
outline: 1px solid var(--incoming-chat-border);
  border: none !important;
  text-decoration: none !important;
}


.chats {
  height: 831px;
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 20px;
  margin-bottom: 20px;
  overflow-y: scroll;
  scrollbar-width: thin; 

  scrollbar-color: var(--incoming-chat-border) var(--outgoing-chat-bg);

  /* WebKit (Chrome, Safari) specific styles */
  &::-webkit-scrollbar {
      width: 15px;
      scrollbar-gutter: 80px;
  }

  &::-webkit-scrollbar-thumb {
      background-color: var(--incoming-chat-border);
      border-radius: 4px;
      background: var(--prime-blue-gradiant, linear-gradient(180deg, #3CAFE4 0%, #046593 100%));
  }

  &::-webkit-scrollbar-track {
      background-color: white;

  }
  scroll-margin: 20px;
}


.sender{
  text-align: left;
  padding: 20px;
  width: 100%;
  border-radius: 20px;
  background: #046593;
  color: white;
  margin-bottom: 5px;
  margin-top: 5px;

}
.responseMsg{
  width: 654px;
  display: inline-flex;
padding: 9px 76px 20px 19px;
flex-direction: column;
align-items: flex-start;
gap: 12px;
border-radius: 20px;
background: var(--prime-blue-gradiant, linear-gradient(180deg, #3CAFE4 0%, #046593 100%));


}
.msg{
  display: inline;
}
.send-button{
  background:var(--prime-blue-gradiant, linear-gradient(180deg, #3CAFE4 0%, #046593 100%));
  height: 35px;
  padding: 5px;
  border: none;
  cursor: pointer;
  margin-left: 5px;
}
.send-button:disabled {
  background: #dddddd;
  opacity: 0.7; 
}